<template>
  <BaseLayout title="充值记录" bg="linear-gradient(180deg, #FFF, #EEE 10%, #FFF 50%, #FFF)">

    <van-pull-refresh v-model="state.refreshing" @refresh="onRefresh">
      <van-sticky :offset-top="45">
        <van-search v-model="state.keyword" placeholder="搜索账号或备注信息" background="#FFF" clearable @search="onRefresh" @clear="onRefresh"/>
      </van-sticky>
      <van-list class="list" v-model:loading="state.loading" :finished="state.finished" loading-text="正在请求数据 ..." finished-text="没有更多了" :immediate-check="false" @load="onLoad">
        <div v-for="(item, index) in state.list" :key="index">
          <van-tag class="dt">{{item.dt}}</van-tag>
            <van-cell class="item" :title="item.detail.title" @click="$copy(getDetail(item) + ' ' + item.id + '-' + item.fgid + '-' + item.foid)">
            <template #label><div v-html="getDetail(item)"></div></template>
            <template #extra>
              ￥{{item.amount}}
              <van-icon class="status" :name="getStatus(item).icon" :color="getStatus(item).color"></van-icon>
            </template>
          </van-cell>
        </div>
        <van-empty v-show="state.list.length == 0" :style="{width:'100%'}"></van-empty>
      </van-list>
    </van-pull-refresh>

  </BaseLayout>
</template>

<style scoped>
.list {
  min-height: 500px;
  padding: 5px;
  text-align: center;
}
.item {
  background: #F6F6F6;
  border: solid 1px #EEE;
  border-radius: 5px;
  text-align: left;
}
.dt {
  margin: 20px 0 10px 0;
  background: #F6F6F6;
  color:#666;
}
.status {
  margin: 5px 0 0 5px;
}
</style>

<script>
import {reactive, ref} from 'vue'
import {mapState, mapGetters, mapMutations} from 'vuex'
import BaseLayout from '@/components/BaseLayout.vue'

export default {
  components: {BaseLayout},

  data() {
    const state = reactive({
      refreshing: false,
      loading: false,
      finished: false,
      keyword: "",
      list: [],
      offset: 0,
      limit : 20,
    });
    return {state};
  },

  created() {
    this.onRefresh();
  },

  methods: {
    onRefresh() {
      this.state.loading = true;
      this.state.finished = false;
      this.state.offset = 0;
      this.state.list = [];
      this.onLoad();
    },
    onLoad() {
      this.$http.get("/digital/order?keyword=" + this.state.keyword + "&offset=" + this.state.offset + "&limit=" + this.state.limit).then((data) => {
        this.state.refreshing = false;
        if (data.length == 0 || data.length < this.state.limit)
          this.state.finished = true;
        this.state.loading = false;
        this.state.offset += data.length;

        for (let i in data){
          let item = data[i];
          this.state.list.push(item);
        }
      });
    },
    getStatus(item){
      if (item.status == 0){
        return {name:"正在充值", icon:"underway", color:"#00C"};
      }
      if (item.status == 1){
        return {name:"订单完成", icon:"checked", color:"#0C0"};
      }
      if (item.status == 2){
        return {name:"等待退款", icon:"warning", color:"#C00"};
      }
      return {name:"订单关闭", icon:"clear", color:"#CCC"};
    },
    getDetail(item){
      let detail = "账号：" + item.account + "；";
      detail += (item.cash > 0 ? "现金：" + item.cash : "") + " " + (item.point > 0 ? "积分：" + item.point : "");
      return detail + "<br>" + " [" + this.getStatus(item).name + "] " + item.remark;
    },

    ...mapMutations(["auth"]),
  }
}
</script>
